import { useState } from 'react';

export default function Home() {
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState(null);

  const createTestOrder = async () => {
    setLoading(true);
    setError(null);
    
    try {
      const response = await fetch('http://localhost:4000/test-order', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      
      const data = await response.json();
      
      if (response.ok) {
        setResult(data);
      } else {
        setError(data.error || 'Erro desconhecido');
      }
    } catch (err) {
      setError('Erro de conex√£o: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4">
        <h1 className="text-4xl font-bold text-center mb-8 text-gray-800">
          ü•© Carne F√°cil - Sistema de Senhas
        </h1>
        
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-2xl font-semibold mb-4">Teste do Sistema</h2>
          
          <button
            onClick={createTestOrder}
            disabled={loading}
            className="bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white font-bold py-3 px-6 rounded-lg transition-colors"
          >
            {loading ? 'Criando pedido...' : 'Criar Pedido de Teste com QR Code'}
          </button>
        </div>

        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-6">
            <strong>Erro:</strong> {error}
          </div>
        )}

        {result && (
          <div className="bg-white rounded-lg shadow-md p-6">
            <h3 className="text-xl font-semibold mb-4 text-green-600">
              ‚úÖ Pedido criado com sucesso!
            </h3>
            
            <div className="grid md:grid-cols-2 gap-6">
              <div>
                <h4 className="font-semibold mb-2">Informa√ß√µes do Pedido:</h4>
                <div className="bg-gray-50 p-4 rounded">
                  <p><strong>Loja:</strong> {result.store?.name}</p>
                  <p><strong>Cliente:</strong> {result.order?.customer_name}</p>
                  <p><strong>Senha:</strong> #{result.ticket?.number}</p>
                  <p><strong>ID do Ticket:</strong> {result.ticket?.id}</p>
                  <p><strong>Status:</strong> <span className="bg-yellow-100 text-yellow-800 px-2 py-1 rounded">Aguardando</span></p>
                </div>
                
                {/* Link para p√°gina do cliente */}
                <div className="mt-4 p-4 bg-blue-50 rounded-lg">
                  <p className="font-semibold text-blue-800 mb-2">üîó P√°gina do Cliente:</p>
                  <a 
                    href={result.qrData?.url} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="text-blue-600 hover:text-blue-800 underline break-all"
                  >
                    {result.qrData?.url}
                  </a>
                </div>
              </div>
              
              <div className="text-center">
                <h4 className="font-semibold mb-2">QR Code da Senha:</h4>
                {result.qrCode && (
                  <img 
                    src={result.qrCode} 
                    alt="QR Code da Senha" 
                    className="mx-auto border rounded"
                  />
                )}
                <p className="text-sm text-gray-600 mt-2">
                  Escaneie para acessar a p√°gina da senha
                </p>
              </div>
            </div>
            
            <div className="mt-4">
              <h4 className="font-semibold mb-2">Dados do QR Code:</h4>
              <pre className="bg-gray-100 p-3 rounded text-sm overflow-x-auto">
                {JSON.stringify(result.qrData, null, 2)}
              </pre>
            </div>
          </div>
        )}
        
        {/* Instru√ß√µes */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-6 mt-6">
          <h3 className="text-lg font-semibold mb-3 text-blue-800">üìã Como usar:</h3>
          <ol className="list-decimal list-inside space-y-2 text-blue-700">
            <li>Clique em "Criar Pedido de Teste" para gerar uma nova senha</li>
            <li>O QR Code ser√° gerado automaticamente</li>
            <li>Clique no link da "P√°gina do Cliente" ou escaneie o QR Code</li>
            <li>A p√°gina mostrar√° o status em tempo real (atualiza a cada 3 segundos)</li>
            <li>Use o painel do a√ßougueiro para alterar o status (em breve)</li>
          </ol>
        </div>
      </div>
    </div>
  );
}